.wrapper {
    position: relative;
    padding-top: 50px;
    display: flex;
    width: 900px;
    align-items: center;
    justify-content: space-between;
}

.searchWrapper {
    position: relative;
}

.input {
    width: 430px;
    height: 45px;
    padding-left: 43px;
    padding-right: 20px;
    font-size: 14px;
    color: #5f7289;
    border: 1px solid #DADFE9;
    border-radius: 10px;
}

.searchIcon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 18px;
}

.dashboardWrapper {
    width: 20px;
    height: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    &:hover {
        cursor: pointer;
    }
    div {
        width: 5px;
        height: 5px;
        border-radius: 10px;
        background-color: #227AFF;
    }
}

.notificationWrapper {
    width: 24px;
    height: 24px;
    img {
        width: 100%;
    }
    &:hover {
        cursor: pointer;
    }
}

.accountWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 25px #E8EDF5;
    border-radius: 10px;
    width: 200px;
    height: 60px;
}

.languageWrapper {
    display: flex;
    align-items: center;
    &:hover {
        cursor: pointer;
    }
    img {
        width: 28px;
    }
    span {
        color: #2E3B50;
        font-weight: 600;
        margin-left: 9px;
    }
}

.accountInfo {
    margin-left: 18px;
    span {
        font-weight: 600;
        font-size: 14px;
        color: #2E3B50;
    }
    p {
        font-weight: 400;
        font-size: 12px;
    }
    &:hover {
        cursor: pointer;
    }
}

.avatarWrapper {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-color: #757575;
    img {
        width: 100%;
    }
}

.dashboardPopupWrapper {
    transition: all 0.1s ease;
    position: absolute;
    z-index: 5;
    right: 415px;
    top: calc(100% - 3px);
    padding: 12px 16px;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.07);
    border-radius: 15px;
    background: #fff;
    display: flex;
    flex-direction: column;
}

.dashboardPopupItem {
    border: 1px solid #227AFF;
    background: #DDECFF;
    border-radius: 100px;
    font-weight: 400;
    font-size: 14px;
    text-align: center;
    padding: 10px 30px;
    margin-bottom: 5px;
    &:last-child {
        margin-bottom: 0;
    }
    &:hover {
        cursor: pointer;
    }
}

@keyframes exit {
    0% {
        opacity: 1;
        pointer-events: all;
    }
    100% {
        opacity: 0;
        pointer-events: none;
    }
}
